<template>
  <div>
    <div>
      上传 txt文件，到富文本中编辑
    </div>
    <el-upload
      class="upload-demo"
      accept=".txt"
      action=""
      :on-change="handleChange"
      :file-list="fileList"
      :auto-upload="false"
      :multiple="true"
    >
      <el-button style="margin:20px 0" size="small" type="primary"
        >点击上传</el-button
      >
    </el-upload>

    <quill-editor class="mt15" v-model="content" :options="editorOption">
    </quill-editor>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: "",
      editorOption: {},
      fileList: []
    };
  },
  methods: {
    onEditorChange() {},
    // 改变文件
    handleChange(file, fileList) {
      let reader = new FileReader();
      if (typeof FileReader === "undefined") {
        this.$message({
          type: "info",
          message: "您的浏览器不支持文件读取。"
        });
        return;
      }
      reader.readAsArrayBuffer(file.raw); //读任意文件
      let _this = this;
      reader.onload = function(e) {
        let ints = new Uint8Array(e.target.result);
        let snippets = new TextDecoder("utf8").decode(ints);
        _this.content += snippets;
      };
    }
  },
  created() {}
};
</script>
<style lang="less" scoped></style>



